.navbar-container {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
    padding: 15px;
    box-shadow: inset rgba(0, 0, 0, 0.5) 0px 5px 10px;
    background: #0d428c;
    transition: .5s;
}

.navbar-container.expanded {
    transition: .5s;
    width: 15%;
}

.navbar-container {
    transition: .5s;
    width: 5%;
}

.navbar-container .toggle-navbar-button-container {
    width: 170%;
    display: flex;
    justify-content: flex-end;
    padding: 5px;
    margin: 5px 0;
    transition: .5s;
}

.navbar-container.expanded .toggle-navbar-button-container {
    width: 115%;
    display: flex;
    justify-content: flex-end;
    padding: 5px;
    margin: 5px 0;
    transition: .5s;
}

.navbar-container .toggle-navbar-button-container .toggle-navbar-button {
    width: 50px;
    height: 50px;
    background: white;
    border-radius: 5%;
    box-shadow: rgba(0, 0, 0, 0.7) 0px 5px 10px;
    cursor: pointer;
    position: relative;
    z-index: 1;
    transition: .5s;
}

.navbar-container .toggle-navbar-button-container  .toggle-navbar-button img {
    width: 50px;
    transition: .5s;
}

.navbar-container.expanded .toggle-navbar-button-container  .toggle-navbar-button img {
    transform: rotate(180deg);
    transition: .5s;
}

.navbar-container ul {
    list-style-type: none;
}

.navbar-container h2 {
    /* display: none; */
    visibility: hidden;
    font-size: 26px;
    font-family: 'Kaushan Script', cursive;
    margin: 10px 0;
    color: white;
    text-align: center;
    padding: 10px;
    text-shadow: rgba(0, 0, 0, .7) 1px 1px;
}

.navbar-container.expanded h2 {
    display: block;
    visibility: visible;
}

.navbar-container .website-icon {
    width: 80px;
    box-shadow: rgba(0, 0, 0, 0.7) 0px 5px 15px;
    border-radius: 5px;
    background: rgb(34, 34, 34);
    transition: .5s;
}

.navbar-container.expanded .website-icon-container {
    display: flex;
    justify-content: center;
    transition: .5s;
}

.navbar-section-header {
    color: white;
    text-align: right;
    font-size: 16px;
    text-transform: uppercase;
    text-decoration: none;
    padding: 5px;
    margin: 7px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.navbar-section-header span {
    font-size: 26px;
}

.navbar-section-header:hover {
    cursor: pointer;
} 

.navbar-section-underline {
    border-top: 1px solid white;
}

.navbar-section-list {
    background: white;
    padding: 10px;
    box-shadow: inset rgba(0, 0, 0, 0.5) 0px 2px 5px;
    list-style-type: none;
    /* list-style-type: ""; */
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    transition: 1s;
    margin-bottom: 15px;
}

.navbar-section-list li {
    margin: 2px;
    width: 85%;
    display: flex;
    justify-content: flex-end;
    border-bottom: 1px solid black;
    border-right: 1px solid black;
    padding: 2px;
    background: white;
}

.navbar-section-list li a {
    background: none;
    text-decoration: none;
    border: none;
    color: black;
    font-size: 14px;
    text-align: right;
    padding-right: 5px;
    cursor: pointer;
}

.navbar-section-list li a:focus {
    outline: none;
}

.collapsed-main-navbar .collapsed-icon {
    display: flex;
    justify-content: center;
    cursor: pointer;
    padding: 5px;
    text-decoration: none;
}

.collapsed-main-navbar .collapsed-icon.home svg {
    color: white;
    margin: 5px;
    font-size: 36px;
    padding: 5px;
}

.collapsed-main-navbar li svg {
    color: white;
    margin: 5px;
    width: 35px;
    height: 35px;
    font-size: 40px;
    padding: 5px;
}

@media (max-width: 1155px) {
    .navbar-container.expanded {
        width: 20%;
    }
    
    .navbar-container .website-icon {
        width: 70px;
    }

    .navbar-container h2 {
        font-size: 20px;
        display: block;
        visibility: hidden;
    }

    .navbar-container.expanded h2 {
        font-size: 20px;
        visibility: visible;
    }
    
    .navbar-section-header {
        font-size: 14px;
        padding: 2px;
    }

    .navbar-section-header span {
        font-size: 20px;
    }

    .collapsed-main-navbar .collapsed-icon.home svg {
        margin: 5px;
        font-size: 30px;
        padding: 5px;
    }

    .collapsed-main-navbar li svg {
        margin: 5px;
        width: 30px;
        height: 30px;
        font-size: 35px;
        padding: 5px;
    }

    .collapsed-main-navbar .collapsed-icon {
        padding: 0px;
    }

    .navbar-container .toggle-navbar-button-container {
        width: 230%;
        padding: 5px;
    }
    
    .navbar-container.expanded .toggle-navbar-button-container {
        width: 120%;
        padding: 5px;
    }
    
    .navbar-container .toggle-navbar-button-container .toggle-navbar-button {
        width: 40px;
        height: 40px;
    }
    
    .navbar-container .toggle-navbar-button-container  .toggle-navbar-button img {
        width: 40px;
    }
}

@media (max-width: 992px) {
    .navbar-container,
    .navbar-container.expanded {
        width: 100%;
        min-height: 80px;
    }

    .navbar-container {
        /* height: 80px; */
    }

    .navbar-container.expanded {
        height: 100%;
    }

    .navbar-container .main-navbar-header {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    .navbar-container h2,
    .navbar-container.expanded h2 {
        display: block;
        margin: 0;
        visibility: visible;
    }

    .navbar-container.expanded .toggle-navbar-button-container,
    .navbar-container .toggle-navbar-button-container {
        width: 10%;
        margin: 0;
        display: flex;
        justify-content: center;
    }

    .navbar-container .toggle-navbar-button-container.top, 
    .navbar-container.expanded .toggle-navbar-button-container.top {
        width: 10%;
    }

    .navbar-container .toggle-navbar-button-container .toggle-navbar-button {
        width: 50px;
        height: 50px;
        box-shadow: rgba(0, 0, 0, 0.7) 0px 5px 10px;
    }
    
    .navbar-container .toggle-navbar-button-container  .toggle-navbar-button img {
        width: 50px;
        transform: rotate(90deg);
    }
    
    .navbar-container.expanded .toggle-navbar-button-container  .toggle-navbar-button img {
        transform: rotate(270deg);
    }

    .navbar-container.expanded .toggle-navbar-button-container.bottom {
        align-self: flex-end;
    }

    .navbar-container.expanded .toggle-navbar-button-container.bottom,
    .navbar-container.expanded .toggle-navbar-button-container.bottom .toggle-navbar-button,
    .navbar-container.expanded .toggle-navbar-button-container.bottom .toggle-navbar-button img {
        display: flex;
        justify-content: center;
    }

    .navbar-container .toggle-navbar-button-container.bottom,
    .navbar-container .toggle-navbar-button-container.bottom .toggle-navbar-button,
    .navbar-container .toggle-navbar-button-container.bottom .toggle-navbar-button img {
        display: none;
    }

    .navbar-container .collapsed-main-navbar {
        display: none;
    }

    .navbar-container .website-icon, 
    .navbar-container .website-icon-container {
        width: 60px;
    }

    .navbar-container .expanded-main-navbar {
        margin: 10px 0;
    }
}

@media (max-width: 414px) {
    .navbar-container .toggle-navbar-button-container .toggle-navbar-button {
        width: 40px;
        height: 40px;
        box-shadow: rgba(0, 0, 0, 0.7) 0px 5px 10px;
    }

    .navbar-container .toggle-navbar-button-container  .toggle-navbar-button img {
        width: 40px;
        transform: rotate(90deg);
    }

    .navbar-container .website-icon, 
    .navbar-container .website-icon-container {
        width: 50px;
    }

    .navbar-container h2,
    .navbar-container.expanded h2 {
        font-size: 22px;
    }
}
